<template>
	<div>
		<Nav navtitle=""></Nav>
		<div class="wrap" id="wrap" @mouseenter="arrappend" @mouseleave="arrhide">
			<div class="slide" id="slide">
				<ul>
					<li v-for="(item,index) in sdarr" :key=index>
						<img :src="item" alt="" />
					</li>
				</ul>
				<div class="arrow" id="arrow">
					<a href="javascript:;" class="prev" @click="prev"></a>
					<a href="javascript:;" class="next" @click="next"></a>
				</div>

			</div>
			<div>
				<p class="titlep">{{this.content}}</p>
				<p class="timep">{{this.detaildate}}</p>
			</div>
		</div>

	</div>
</template>

<script>
import Nav from '../common/nav.vue'
export default {
  data () {
    return {
      flagr: true,
      sdarr: [],
      content: '',
      detaildate: '',
      json: [
        //  5
        {
          //  1
          width: 400,
          top: 100,
          left: -50,
          opacity: 20,
          z: 2
        },
        {
          // 2
          width: 500,
          top: 70,
          left: 0,
          opacity: 80,
          z: 3
        },
        {
          // 3
          width: 600,
          top: 20,
          left: 300,
          opacity: 100,
          z: 4
        },
        {
          // 4
          width: 500,
          top: 70,
          left: 700,
          opacity: 80,
          z: 3
        },
        {
          // 5
          width: 400,
          top: 100,
          left: 850,
          opacity: 20,
          z: 2
        }
      ]
    }
  },
  methods: {
    animate (obj, json, fn) {
      //     2  attr     3   
      clearInterval(obj.timer)
      obj.timer = setInterval(function () {
        let flag = true //     
        //   30 
        for (var k in json) {
          // k      json[k] 
          //  k  width
          //  k     height
          //  leader  =  600 -  100
          // leader = leader + (target - leader )  /10
          //  
          // target     leader 
          //    
          // 
          //  var leader = obj.style[attr]  
          var leader = 0
          //      0~1       
          if (k === 'opacity') {
            // 
            leader =
              Math.round(window.getComputedStyle(obj, null)[k] * 100) || 100
            // opacity   1     1* 100  = 100
            //   0.3   100   30  
          } else {
            leader = parseInt(window.getComputedStyle(obj, null)[k]) || 0
            //       0
          }
          // px   parseInt(25px)

          var step = (json[k] - leader) / 10
          step = step > 0 ? Math.ceil(step) : Math.floor(step)
          leader = leader + step
          if (k === 'opacity') {
            //   px
            obj.style.opacity = leader / 100
            //                    opacity: 0.4
            //                    filter: alpha(opacity = 40)     /* 40   0.4*/
            obj.style.filter = 'alpha(opacity = ' + leader + ')'
          } else if (k === 'zIndex') {
            // 
            obj.style.zIndex = json[k] //  
          } else {
            obj.style[k] = leader + 'px' // 
          }
          if (leader !== json[k]) {
            //  
            //  for in     for in 
            //  
            flag = false
          }
        }
        // console.log(flag)
        if (flag) {
          clearInterval(obj.timer)
          if (fn) {
            //       
            fn() //   fn + ()
          }
        }
      }, 30)
    },

    move (x) {
      let slide = document.getElementById('slide')
      let lis = slide.children[0].children
      if (x !== undefined) {
        //    json
        if (x) {
          //  json   
          this.json.unshift(this.json.pop())
        } else {
          //  json
          this.json.push(this.json.shift())
        }
      }
      //  json  
      for (let i = 0; i < this.json.length; i++) {
        this.animate(
          lis[i],
          {
            width: this.json[i].width,
            top: this.json[i].top,
            left: this.json[i].left,
            opacity: this.json[i].opacity,
            zIndex: this.json[i].z
          },
          function () {}
        )
      }
    },
    arrappend () {
      document.getElementById('arrow').style.opacity = 1
    },
    arrhide () {
      document.getElementById('arrow').style.opacity = 0
    },
    prev () {
      //				if(this.flagr === true) { // 
      this.move(true)
      //					this.flagr = false
      //				}
    },
    next () {
      //				if(this.flagr === true) { // 
      this.move(false)
      //					this.flagr = false
      //				}
    }
  },
  components: {
    Nav
  },
  created () {
    this.content = this.$route.params.content.content
    this.detaildate = this.$route.params.content.detaildate
    this.sdarr.push(this.$route.params.content.zimgurl1)
    this.sdarr.push(this.$route.params.content.zimgurl2)
    this.sdarr.push(this.$route.params.content.zimgurl3)
    this.sdarr.push(this.$route.params.content.zimgurl4)
    this.sdarr.push(this.$route.params.content.zimgurl5)
  },
  mounted () {
    this.move()
  }
}
</script>

<style scoped>
ol,
ul {
  list-style: none;
}

a {
  text-decoration: none;
}

fieldset,
img {
  border: 0;
  vertical-align: top;
}

a,
input,
button,
select,
textarea {
  outline: none;
}

a,
button {
  cursor: pointer;
}

.wrap {
  width: 1200px;
  margin: 100px auto;
}

.slide {
  height: 680px;
  position: relative;
}

.slide li {
  position: absolute;
  left: 200px;
  top: 0;
}

.slide li img {
  width: 100%;
}

.arrow {
  opacity: 0;
}

.prev,
.next {
  width: 76px;
  height: 112px;
  position: absolute;
  top: 50%;
  margin-top: -56px;
  background: url('../../../images/prev.png') no-repeat;
  z-index: 99;
}

.next {
  right: 0;
  background-image: url('../../../images/next.png');
}
.titlep {
  color: #ee882a;
  text-align: center;
}
.timep {
  color: #000000;
  text-align: center;
  font-size: 14px;
}
</style>



