export default {
  methods: {
    changepage (value, index) {
      // page
      this.fliter.page = value
      this.getPage(index)
      this.getData()
    },
    prev () {
      if (this.li7 >= 7) { // 7
        if (this.fliter.page > 1) {
          this.fliter.page = this.fliter.page - 1// 
          let index
          if (this.fliter.page <= 3) { // 3
            index = this.fliter.page
            this.getPage(index)
          } else if (this.fliter.page > 3 && this.fliter.page < this.li7 - 2) { // 
            index = 5
            this.getPage(index)
          } else if (this.fliter.page >= this.li7 - 2) { // 
            index = 7 - (this.li7 - this.fliter.page)
            this.getPage(index)
          }
        } else {
          this.fliter.page = 1
        }
        this.getData()
      } else { //  
        if (this.fliter.page <= 1) {
          this.fliter.page = 1
          let index = 1
          this.getPage(index)
        } else {
          this.fliter.page = this.fliter.page - 1
          let index = this.fliter.page
          this.getPage(index)
        }
        this.getData()
      }
    },
    next () {
      if (this.li7 >= 7) { // 7
        if (this.fliter.page < this.li7) {
          this.fliter.page = this.fliter.page + 1// 
          let index
          if (this.fliter.page <= 3) { // 3
            index = this.fliter.page
            this.getPage(index)
          } else if (this.fliter.page > 3 && this.fliter.page < this.li7 - 2) { // 
            index = 5
            this.getPage(index)
          } else if (this.fliter.page >= this.li7 - 2) { // 
            index = 7 - (this.li7 - this.fliter.page)
            this.getPage(index)
          }
        } else {
          this.fliter.page = this.li7
        }
        this.getData()
      } else { //  
        if (this.fliter.page >= this.li7) {
          this.fliter.page = this.li7
          let index = this.li7
          this.getPage(index)
        } else {
          this.fliter.page = this.fliter.page + 1
          let index = this.fliter.page
          this.getPage(index)// 
        }
        this.getData()
      }
    },
    getPage (index) {
      if (this.li7 >= 7) { // 7
        let ii = document.getElementsByClassName('li').length
        if (this.fliter.page <= 3) { // 
          this.li2 = 2// 
          this.li3 = 3
          this.li4 = 4
          this.li5 = 5
          this.li6 = '...'
          for (let i = 0; i < ii; i++) {
            document.getElementsByClassName('li')[i].id = ''
          }
          document.getElementsByClassName('li')[index - 1].id = 'active'
        } else if (this.fliter.page > 3 && this.fliter.page < this.li7 - 2) { // 
          this.li2 = '...'
          this.li3 = this.fliter.page - 1
          this.li4 = this.fliter.page
          this.li5 = this.fliter.page + 1
          this.li6 = '...'
          if (this.fliter.page > 4) { // id
            for (let i = 0; i < ii; i++) {
              document.getElementsByClassName('li')[i].id = ''
            }
            document.getElementsByClassName('li')[3].id = 'active'// id
          } else if (this.fliter.page === 3) {
            for (let i = 0; i < ii; i++) {
              document.getElementsByClassName('li')[i].id = ''
            }
            document.getElementsByClassName('li')[index - 1].id = 'active'// id
          }
        } else if (this.fliter.page >= this.li7 - 2) { // N
          this.li2 = '...'
          this.li3 = this.li7 - 4
          this.li4 = this.li7 - 3
          this.li5 = this.li7 - 2
          this.li6 = this.li7 - 1
          for (let i = 0; i < ii; i++) {
            document.getElementsByClassName('li')[i].id = ''
          }
          document.getElementsByClassName('li')[index - 1].id = 'active'// id
        } else {
          let ii = document.getElementsByClassName('li').length
          for (let i = 0; i < ii; i++) {
            document.getElementsByClassName('li')[i].id = ''
          }
          document.getElementsByClassName('li')[index - 1].id = 'active'
        }
      }
    },
    getData () {
      this.$http.post(`http://120.79.22.222:3000/${this.apimodel}/list`, this.fliter).then(res => { // 
        let ii = res.data.rows.length
        for (let i = 0; i < ii; i++) {
          res.data.rows[i].num = i
        }
        this.fliter.data6 = res.data.rows
        this.li7 = res.data.pages
        if (this.li7 < 7) {
          for (let i = 1; i < this.li7; i++) {
            this.pagenum.push(i)
          }
        }
      })
    }
  }
}



