<template>
	<div>
		<Nav navtitle=""></Nav>
		<div class="traincontent">
			<p class="traincontent-title"></p>
			<p class="traincontent-tcontent"></p>
			<div v-for="(item,index) in this.fliter.data6" :key=index>
				<p class="traindetail-title"></p>
				<p class="traindetail-title">{{item.edpattern}}</p>
				<p class="traincontent-tcontent">{{item.edcontent}}</p>
				<div class="traindetail-content">
					<img :src="item.edimgurl">
					<img :src="item.edimgurl1">
				</div>
			</div>
			<p class="traincontent-bottom">
				
			</p>
		</div>
	</div>
</template>

<script>
import Nav from '../common/nav.vue'
export default {
  data () {
    return {
      information: '',
      fliter: {
        data6: [],
        type: '2',
        limit: 2
      }
    }
  },
  components: {
    Nav
  },
  created () {
    this.$http.post('http://120.79.22.222:3000/hr/list', this.fliter).then(res => {
      this.fliter.data6 = res.data.rows
      this.information = res.data.rows[0].edreson
    })
  }
}
</script>

<style scoped>
p {
  margin: 0;
  padding: 0;
}

.contact-title {
  display: flex;
  width: 440px;
  margin: 0 auto;
  position: relative;
  margin-top: 100px;
}

.contact-title p {
  line-height: 24px;
  font-size: 24px;
  flex: 1;
  color: #f1662c;
  text-shadow: 1px 1px 1px #f1662c;
}

.contact-title img {
  height: 20px;
  margin: 3px 40px;
}
.traincontent {
  width: 1420px;
  margin: 30px auto 0;
}
.traincontent-title {
  color: #666666;
  text-shadow: 0.5px 0.5px 0.5px #666666;
}
.traincontent-tcontent {
  margin-top: 10px;
  font-size: 14px;
  color: #666666;
  text-shadow: -0.2px 0.2px 0.2px #666666;
}
.traindetail-title {
  margin-top: 30px;
  color: #666666;
  text-shadow: 0.5px 0.5px 0.5px #666666;
}
.traindetail-content {
  margin-top: 50px;
  display: flex;
}
.traincontent-bottom {
  margin-top: 45px;
  font-size: 14px;
  color: #666666;
  text-shadow: -0.2px 0.2px 0.2px #666666;
}
</style>



