<template>
<div>
	<div v-if="width<1500&&width>640">
		<Nav navtitle=''></Nav>
		<div class="bottom">
			<p class="stp">445402013</p>
			<p class="stp">7195820082924413XX2041470300409</p>
		</div>
	</div>
  <div v-if="width>=1500">
		<Nav navtitle=''></Nav>
		<div class="midbottom">
			<p class="midstp">445402013</p>
			<p class="midstp">7195820082924413XX2041470300409</p>
		</div>
	</div>
  </div>
</template>

<script>
import Nav from '../common/nav.vue'
export default {
  data () {
    return {
      width: 1920
    }
  },
  components: {
    Nav
  },
  created () {
    this.width = document.documentElement.offsetWidth
  },
  updated () {
    window.onresize = () => {
      this.width = document.documentElement.offsetWidth
    }
  }
}
</script>

<style scoped>
p {
  margin: 0;
  padding: 0;
}
.bottom {
  width: 900px;
  margin: 0 auto;
  padding: 0 40px;
}
.stp {
  margin-top: 45px;
  font-size: 14px;
  color: #666666;
  line-height: 25px;
  text-indent: 2em;
}
.midbottom {
  width: 1420px;
  margin: 0 auto;
}
.midstp {
  margin-top: 45px;
  font-size: 14px;
  color: #666666;
  line-height: 25px;
}
</style>



